body {
    background-color: #39aab1;
}

#profile-page {
    height: 100vh;
    padding-top: 20vh;
    overflow: hidden
}

.bg-t-l {
    top: -50%;
    left: -5%;
}

.bg-b-r {
    bottom: -50%;
    right: -5%;
}

#profile-page .card-border-top {
    border-radius: 1rem 1rem 0 0;
}

.profile-avatar {
    margin-top: -48px;
    border-width: 5px !important;
}

#profile-page .card-border-bottom {
    border-radius: 0 0 1rem 1rem;
}

.attribution {
    font-size: 11px;
    text-align: center;
}

.attribution a {
    color: hsl(228, 45%, 44%);
}